.camera-wrapper {
  position: relative;
  overflow: hidden;
}

.camera-control {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  padding: 0 .6rem;
  bottom: .6rem;
}

.explore-btn {
  width: 1.4rem;
  height: 1.4rem;
  border: none;
  border-radius: 50%;
  background: url('./images/pokeball.png') no-repeat;
  background-size: cover;
  -webkit-box-shadow: 0 2px 7px rgba(0, 0, 0, 0.25);
          box-shadow: 0 2px 7px rgba(0, 0, 0, 0.25);
}



